import utils from './utils.js';

class Route {
	constructor(path, handler) {
		this.path = path;
		this.handler = handler;
	}

	setProps(newProps) {
		this.props = newProps;
	}

	handle(path) {
		if (typeof this.handler == 'function') {
			return this.handler(
				utils.buildContext(this.route, path, this.props)
			);
		}
	}
}

export default Route;
